import React from 'react';
import { Rate } from 'antd';
interface PinProps extends React.ComponentProps<typeof Rate> {
  checkout: boolean;
  onCheckedChange?: (checkouot: boolean) => void;
}
//封装组件要有透穿的能力，就是暂时先定义这些，假如我有其他没有定义的呢。应该在最后面统一...
export const Pin = ({ checkout, onCheckedChange, ...resultProps }: PinProps) => {
  return (
    <Rate
      count={1}
      value={checkout ? 1 : 0}
      onChange={(num) => onCheckedChange?.(Boolean(num))}
      {...resultProps}
    />
  );
};
