import { Dropdown, Menu, Table, TableProps, Modal } from 'antd';
import React from 'react';
import dayjs from 'dayjs';
import { Link, Outlet } from 'react-router-dom';
import { useProjectoModal } from 'utils/hooks/useUrlQueryParam';
import { useDeleteProject, useEditProject, useProjectQueryKey } from 'api/modules/jira/homeList';
import { Pin } from 'components/Pin/Pin';
import { ButtonNoPadding } from 'components/Row/Row';
import { TableContainer } from './style';
export interface Lists {
  name: string;
  personId: number;
  organization: string;
  id: number;
  created: number;
  pin: boolean;
}
export interface User {
  name: string;
  age: number;
  id: number;
}
//interface可以继承，而type不行
interface listProps extends TableProps<Lists> {
  user: User[];
  retry?: () => void;
}
export const List = ({ user, ...props }: listProps) => {
  const { mutate } = useEditProject(useProjectQueryKey());

  //大坑 props里面的loading必须是这样写loading
  //函数珂里化
  // const pinProjects = (id: number) => (pin: boolean) => mutate({ id, pin });
  return (
    <TableContainer>
      <Table
        rowKey={'id'}
        pagination={false}
        columns={[
          {
            title: <Pin checkout disabled />,
            render(value, project) {
              return (
                <Pin
                  checkout={project.pin}
                  onCheckedChange={(pin) => {
                    mutate({ id: project.id, pin });
                  }}
                />
              );
            },
          },
          {
            title: '名称',
            sorter: (a, b) => a.name.localeCompare(b.name),
            render(value, project) {
              return <Link to={`/jira?id=${project.id}&type=kanban`}>{project.name}</Link>;
            },
          },
          {
            title: '部门',
            dataIndex: 'organization',
            sorter: (a, b) => a.name.localeCompare(b.name),
          },
          {
            title: '负责人',
            render(value, Item) {
              return (
                <span>
                  {user.find((user: { id: number }) => user.id === Item.personId)?.name || '无'}
                </span>
              );
            },
          },
          {
            title: '创建时间',
            render(value, Item) {
              return <span>{Item.created ? dayjs(Item.created).format('YYYY-MM-DD') : '无'}</span>;
            },
          },
          {
            title: '操作',
            render(value, project) {
              return <More {...{ project }} />;
            },
          },
        ]}
        {...props}
      />
    </TableContainer>
  );
};
const More = ({ project }: { project: Lists }) => {
  const { mutate: deleteProject } = useDeleteProject(useProjectQueryKey());
  const { starEdit } = useProjectoModal();
  const editProject = (id: number) => () => starEdit(id);
  const confirmDeleteProject = (id: number) => () => {
    return Modal.confirm({
      title: '确定删除这个项目吗?',
      content: '点击确定删除',
      okText: '确定',
      onOk() {
        deleteProject({ id });
      },
    });
  };
  return (
    <Dropdown
      overlay={
        <Menu>
          <Menu.Item key={'edit'}>
            <ButtonNoPadding type={'link'} onClick={editProject(project.id)}>
              编辑
            </ButtonNoPadding>
          </Menu.Item>
          <Menu.Item key={'delete'}>
            <ButtonNoPadding type={'link'} onClick={confirmDeleteProject(project.id)}>
              删除
            </ButtonNoPadding>
          </Menu.Item>
        </Menu>
      }
    >
      <ButtonNoPadding type={'link'}>...</ButtonNoPadding>
    </Dropdown>
  );
};
