import React, { useRef } from 'react';
import _ from 'lodash';
import { Form, Input, message } from 'antd';
import { LongButton } from './style';

import { useAsync } from 'utils/hooks/useAsync';
import { useDebounces, useDocumentTitle } from 'utils/hooks/authUtils';
import { useAuth } from 'utils/hooks/useContext/authContext';
//TS对象结构，应该在对象后面定义类型
// { setError }: { setError: (error: Error) => void }
const LoginScreen = ({ setError }: { setError: (error: Error) => void }) => {
  // const Login = async (param: { username: string; password: string }) => {
  // const browserMatch = uaMatch(userAgent.toLowerCase());
  // _.set(param, "deviceType", "PC");
  // _.set(param, "browserType", browserMatch.browser);
  // _.set(param, "browserVersion", browserMatch.version);
  // const {data} = await auth_login.login(param)
  //   if (data.head.code === 401 || data.head.code === 500) {
  //     message.error(`${data.head.msg}`);
  //   } else {
  //     message.success("登录成功");
  //   }
  // };
  /**
   * 设置文档Title
   */
  useDocumentTitle('Valuableness', false);
  const { login } = useAuth();
  const { run, isLoading } = useAsync(undefined, { throwOnError: true });

  // const handSubmit = (event: FormEvent<HTMLFormElement>) => {
  //   //阻止默认事件
  //   event.preventDefault();
  //   const loginName = (event.currentTarget.elements[0] as HTMLInputElement)
  //     .value;
  //   const pwd = (event.currentTarget.elements[1] as HTMLInputElement).value;
  //   login({ loginName, pwd });
  // };

  /**
   *
   * @param async 这里是异步，结果现在还没result，直接catch肯定是普获不到的，直接同步等待result执行完毕在await，在普获erroe，坑
   */
  const HandSubmit = useDebounces(async (values: { username: string; password: string }) => {
    try {
      await run(login(values));
    } catch (error: any) {
      message.error(error.message);
    }
  }, 1000);
  return (
    <>
      <Form onFinish={HandSubmit}>
        <Form.Item
          name={'username'}
          rules={[{ required: true, message: '请输入用户名' }]}
          // eslint-disable-next-line react/no-children-prop
          children={<Input type="text" placeholder={'用户名'} id={'username'} />}
        />
        <Form.Item
          name={'password'}
          rules={[{ required: true, message: '请输入密码' }]}
          // eslint-disable-next-line react/no-children-prop
          children={<Input placeholder={'密码'} type="password" id={'password'} />}
        ></Form.Item>
        <LongButton {...{ loading: isLoading }} htmlType={'submit'} type={'primary'}>
          登录
        </LongButton>
      </Form>
    </>
  );
};

export default LoginScreen;
