import { QueryKey, useMutation } from 'react-query';
import { useHttp } from 'utils/hooks/useHttp';
import { useUrlQueryParam } from 'utils/hooks/useUrlQueryParam';
import { useProject } from 'api/modules/jira/homeList';
import { useLocation } from 'react-router-dom';
import { useMemo, useCallback } from 'react';
import { useDebounce } from 'utils/hooks/authUtils';
import { useTask } from 'api/modules/jira/task';
import { useConfig } from 'utils/hooks/useOptimisticOptions';
import { reorder } from './reorder';
import { Task } from 'type/jira/task';

export interface SortProps {
	fromId: number;
	type: 'before' | 'after';
	referenceId: number;
	fromKanbanId?: number;
	toKanbanId?: number;
}
// 看板模块
// 获取jira页面ID,详情用来report 请求
export const useProjectIdInUrl = () => {
	const { search } = useLocation();
	const id = search.match(/id=(\d+)/)?.[1];
	return Number(id);
};
// 加这四个hooks，是因为一上来就直接请求所有的，不太好，现在直接根据传入的id，动态返回数据然后渲染
// 这里根据url中Id来直接换取请求
export const useProjectInUrl = () => useProject(useProjectIdInUrl());

export const useKanbanSearchParams = () => ({ projectId: useProjectIdInUrl() });

export const useKanbanQueryKey = () => ['kanbans', useKanbanSearchParams()];

// 通过改变这四个对象，来实现视图更新
export const useTasksSearchParams = () => {
	const [{ name, typeId, processorId, tagId }, setParam] = useUrlQueryParam(['name', 'typeId', 'processorId', 'tagId']);
	const projectId = useProjectIdInUrl();
	const debouncedName = useDebounce(name, 2000);
	return useMemo(
		() => ({
			projectId,
			typeId: Number(typeId) || undefined,
			processorId: Number(processorId) || undefined,
			tagId: Number(tagId) || undefined,
			name: debouncedName
		}),
		[projectId, typeId, processorId, tagId, debouncedName]
	);
};

export const useTasksQueryKey = () => ['tasks', useTasksSearchParams()];

// 点击Task,进行编辑,通过点击列表传入id,id变化，当前组件重新render(),视图变化
export const useTasksModal = () => {
	const [{ editingTaskId }, setEditingTaskId] = useUrlQueryParam(['editingTaskId']);
	const { data: editingTask, isLoading } = useTask(Number(editingTaskId));
	const startEdit = useCallback(
		(id: number) => {
			setEditingTaskId({ editingTaskId: id });
		},
		[setEditingTaskId]
	);
	const clone = useCallback(() => {
		setEditingTaskId({ editingTaskId: '' });
	}, [setEditingTaskId]);
	return {
		editingTaskId,
		startEdit,
		clone,
		isLoading,
		editingTask
	};
};
// 拖拽看板请求
export const useRecorderKanban = (querykey: QueryKey) => {
	const client = useHttp();
	return useMutation(
		(param: SortProps) =>
			client('kanbans/reorder', {
				method: 'PATCH',
				data: param
			}),
		useReorderKanbanConfig(querykey)
	);
};
// 拖拽task请求
export const useRecorderTask = (querykey: QueryKey) => {
	const client = useHttp();
	return useMutation(
		(param: SortProps) =>
			client('tasks/reorder', {
				method: 'PATCH',
				data: param
			}),
		useReorderTaskConfig(querykey)
	);
};

// 拖拽乐观更新
export const useReorderKanbanConfig = (queryKey: QueryKey) =>
	useConfig(queryKey, (target, old) => reorder({ list: old, ...target }));

export const useReorderTaskConfig = (queryKey: QueryKey) =>
	useConfig(queryKey, (target, old) => {
		const orderedList = reorder({ list: old, ...target }) as Task[];
		return orderedList.map(item => (item.id === target.fromId ? { ...item, kanbanId: target.toKanbanId } : item));
	});

// 任务组模块
export const useEpicSearchParams = () => ({ projectId: useProjectIdInUrl() });

export const useEpicQueryKey = () => ['epics', useEpicSearchParams()];
