import React from 'react';
import { useTasksSearchParams } from '../../../utils';
import { useSetUrlSearchParam } from 'utils/hooks/useUrlQueryParam';
import { Row } from 'components/Row/Row';
import { Input } from 'antd';
import { TaskTypeSelect, UserSelect } from 'components/UserSecelt/UserSecelt';
import { Button } from 'style/componends/Button';
// 这套可以用在其他项目
export const SearchPanel = () => {
  // 搜索值
  const searchParams = useTasksSearchParams();
  // 查找onChange 这里一改变，就会重新改变视图，然后吧值给到searchParams对象里面
  // 用到这个hooks的组件会更新，所以试图会变化
  const setSearchParams = useSetUrlSearchParam();
  // 重置
  const reset = () => {
    setSearchParams({
      typeId: undefined,
      processorId: undefined,
      tagId: undefined,
      name: undefined,
    });
  };
  return (
    <Row marginBottom={1} gap>
      <Input
        style={{ width: '20rem' }}
        placeholder="任务名"
        value={searchParams.name}
        onChange={(eve) => setSearchParams({ name: eve.target.value })}
      />
      <UserSelect
        defaultOptionName="经办人"
        value={searchParams.processorId}
        onChange={(eve) => setSearchParams({ processorId: eve })}
      />
      <TaskTypeSelect
        defaultOptionName="类型"
        value={searchParams.typeId}
        onChange={(eve) => setSearchParams({ typeId: eve })}
      />
      <Button onClick={reset}>清除筛选器</Button>
    </Row>
  );
};
