import { useAddTask } from 'api/modules/jira/task';
import React, { useEffect, useState } from 'react';
import { useProjectIdInUrl, useTasksQueryKey } from '../../../utils';
import { Card, Input } from 'antd';
import styled from 'styled-components';
import { ThemeBgcAndText } from 'style/theme/other';
export const CreateTask = ({ kanbanId }: { kanbanId?: number }) => {
  const [name, setName] = useState<string>('');
  const { mutateAsync: addTask } = useAddTask(useTasksQueryKey());
  const projectId = useProjectIdInUrl();
  //  控制弹框打开
  const [inputMode, setInputMode] = useState<boolean>(false);
  // 提交
  const submit = async () => {
    await addTask({ projectId, name, kanbanId });
    setInputMode(false);
    setName('');
  };
  const toggle = () => setInputMode(!inputMode);
  //  弹框发生变化，重置name，
  useEffect(() => {
    if (!inputMode) {
      setName('');
    }
  }, [inputMode]);
  // 弹框关闭，直接return 这一段tsx，render不执行
  if (!inputMode) {
    return <Createaffair onClick={toggle}> +创建事务</Createaffair>;
  }
  return (
    <Card>
      <Input
        // 失去焦点触发的事件
        onBlur={toggle}
        placeholder="需要做些什么"
        // 自动聚焦
        autoFocus
        // 回车健
        onPressEnter={submit}
        value={name}
        onChange={(eve) => setName(eve.target.value)}
      />
    </Card>
  );
};
export const Createaffair = styled.div`
  ${ThemeBgcAndText};
  padding: 1rem;
  font-size: 0.9rem;
  width: calc(100% - 1.5rem);
  margin: 0 auto;
  text-align: center;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
  ${(props) => props.theme.mode === 'dark' && 'filter: brightness(0.5);'};
`;
