import React, { FC, useEffect } from 'react';
import { KanBanScroon } from './kanban/KanBan';
import { EcipScroon } from './epic/Epic';
import { useProjects } from 'api/modules/jira/homeList';
import { useUrlQueryParam } from 'utils/hooks/useUrlQueryParam';
import { DetailLeft, DetailRight, JiraDetailContainer } from './style';
import { Caption } from 'style/componends/Text';
import { FlexDiv, ItemCenterBox } from 'style/componends/Flex';
import { useCampusTagKeyUrl, useRouterType } from 'utils/hooks/authUtils';
import { Menu } from 'antd';

interface Iprops {}
const Scroon: FC<Iprops> = () => {
  const { type: kanbanType } = useCampusTagKeyUrl();
  const [{ type }, setTabKey] = useUrlQueryParam(['type']);
  const handleOnClickAcitveTabKey = (key: 'kanban' | 'epic') => () => {
    const activePathName = key === 'kanban' ? 'kanban' : 'epic';
    setTabKey({ type: activePathName });
  };
  return (
    <JiraDetailContainer>
      <FlexDiv>
        <DetailLeft {...{ diffJiraSelectComp: type }}>
          <Menu selectedKeys={[kanbanType as string]}>
            <Menu.Item key={'kanban'}>
              <ItemCenterBox onClick={handleOnClickAcitveTabKey('kanban')}>看板</ItemCenterBox>
            </Menu.Item>
            <Menu.Item key={'epic'}>
              <ItemCenterBox onClick={handleOnClickAcitveTabKey('epic')}>任务组</ItemCenterBox>
            </Menu.Item>
          </Menu>
        </DetailLeft>
        <DetailRight>
          {type === 'kanban' ? <KanBanScroon /> : <EcipScroon />}
        </DetailRight>
      </FlexDiv>
    </JiraDetailContainer>
  );
};
export default Scroon;
