import { useForm } from 'antd/es/form/Form';
import React, { useEffect } from 'react';
import { useTasksModal, useTasksQueryKey } from '../../../utils';
import { useDeleteTask, useEditTask } from 'api/modules/jira/task';
import { Form, Input, Modal } from 'antd';
import { TaskTypeSelect, UserSelect } from 'components/UserSecelt/UserSecelt';
import { Button } from 'style/componends/Button';
import { CenterBox } from 'style/componends/Flex';
const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
};
export const TaskModal = () => {
  const [form] = useForm();
  const { editingTaskId, clone, editingTask } = useTasksModal();
  const { mutateAsync: editTask, isLoading: edidLoading } = useEditTask(useTasksQueryKey());
  const { mutateAsync } = useDeleteTask(useTasksQueryKey());
  const onCancel = () => {
    clone();
    form.resetFields();
  };
  const onOk = async () => {
    await editTask({ ...editingTask, ...form.getFieldsValue() });
    clone();
  };

  const startDelete = () => {
    onCancel();
    Modal.confirm({
      okText: '确定',
      cancelText: '取消',
      title: '确定删除看板吗',
      onOk() {
        return mutateAsync({ id: Number(editingTaskId) });
      },
    });
  };
  useEffect(() => {
    form.setFieldsValue(editingTask);
  }, [editTask, editingTask, form]);
  return (
    <Modal
      forceRender
      okText={'确认'}
      onCancel={onCancel}
      onOk={onOk}
      cancelText={'取消'}
      visible={!!editingTaskId}
      confirmLoading={edidLoading}
      title={'编辑'}
    >
      <Form {...layout} initialValues={editingTask} form={form}>
        <Form.Item
          label={'任务名'}
          name={'name'}
          rules={[{ required: true, message: '请输入任务名' }]}
        >
          <Input />
        </Form.Item>
        <Form.Item label={'经办人'} name={'processorId'}>
          <UserSelect defaultOptionName={'经办人'} />
        </Form.Item>
        <Form.Item label={'类型'} name={'typeId'}>
          <TaskTypeSelect defaultOptionName={'经办人'} />
        </Form.Item>
        <CenterBox style={{ marginLeft: '220px' }}>
          <Button onClick={startDelete}>删除</Button>
        </CenterBox>
      </Form>
    </Modal>
  );
};
