import React, { useState } from 'react';
import { RegisterScreen } from './register';
import LoginScreen from './login';
import { Divider, Typography } from 'antd';
import {
  Title,
  BackgroundLeft,
  BackgroundRight,
  Container,
  LongButton,
  ShadowCard,
  Header,
} from './style';
import WaveText from 'style/componends/Text/WaveText';
export const UnauthenticatedApp = () => {
  const [isRegister, setIsRegister] = useState(false);
  const [error, setError] = useState<null | Error>(null);
  return (
    <>
      <Container>
        <Header />
        <BackgroundLeft />
        <BackgroundRight />
        <ShadowCard>
          <Title>
            <WaveText content="Jira任务管理系统" />
          </Title>
          {/* {error?<Typography.Text type={"danger"}>{error?.message}</Typography.Text>:null} */}
          {isRegister ? <RegisterScreen /> : <LoginScreen {...{ setError }} />}
          <Divider />
          <LongButton onClick={() => setIsRegister(!isRegister)}>
            {isRegister ? (
              <span>已有账户?{'   '.replace(/ /g, '\u00a0')}点击登录</span>
            ) : (
              <span>没有账户?{'   '.replace(/ /g, '\u00a0')}点击注册</span>
            )}
          </LongButton>
        </ShadowCard>
      </Container>
    </>
  );
};
