import { Select } from 'antd';
import React from 'react';
import { IsVoild } from 'type/home';

//这个属性是直接吧这个select里面的健值对全部提取出来
type SelectProps = React.ComponentProps<typeof Select>;
/**
 * 这里使用Omit是继承了Select ，typeof吧健值对全部取出来，然后发现被继承的IdSelect里面的属性，跟Select里面的一些属性冲突，所以，干脆直接使用Omit，直接删除指定的这些属性，留下剩下的
 */
interface IdSelectProps extends Omit<SelectProps, 'value' | 'onChange' | 'options'> {
  value?: IsVoild;
  onChange?: (value?: number) => void;
  defaultOptionName: string;
  options?: any;
  // {
  //   name: string;
  //   id: number;
  // }[]; //这里可以对象数组
}
/**
 *
 * @param props value 可以传入多种类型
 * onChange只会回调number｜underined类型
 * 当isNaN（Number（value））为true得时候，代表选择默认类型。
 * 当选择默认类型的时候，onChange会回调undefined
 */

export const toNumber = (value: unknown) => (isNaN(Number(value)) ? 0 : Number(value));

export const IdSelect = (props: IdSelectProps) => {
  const { value, onChange, defaultOptionName, options, ...restProps } = props;
  return (
    <Select
      value={toNumber(value)}
      onChange={(value) => onChange?.(toNumber(value) || undefined)}
      {...restProps}
    >
      {defaultOptionName ? <Select.Option value={0}>{defaultOptionName}</Select.Option> : null}
      {options?.map((options: { name: string; id: number }) => (
        <Select.Option key={options.id} value={options.id}>
          {options.name}
        </Select.Option>
      ))}
    </Select>
  );
};
