import { Button, Form, Input, message } from 'antd';
import { LongButton } from './style';
import { useAsync } from 'utils/hooks/useAsync';
import { useAuth } from 'utils/hooks/useContext/authContext';
import { useDebounces } from 'utils/hooks/authUtils';
export const RegisterScreen = () => {
	const registers = async (param: { username: string; password: string }) => {
		// const browserMatch = uaMatch(userAgent.toLowerCase());
		// _.set(param, "deviceType", "PC");
		// _.set(param, "browserType", browserMatch.browser);
		// _.set(param, "browserVersion", browserMatch.version);
		// const {data} = await auth_login.login(param)
		//   if (data.head.code === 401 || data.head.code === 500) {
		//     message.error(`${data.head.msg}`);
		//   } else {
		//     message.success("@");
		//   }
	};

	const { register } = useAuth();
	const { run, isLoading } = useAsync(undefined, { throwOnError: true });
	const handSubmit = useDebounces(
		({ cpassword, ...values }: { username: string; password: string; cpassword: string }) => {
			if (cpassword !== values.password) {
				message.error('两次输入密码不相同');
				//直接不执行，退出当前执行stack
				return;
			}
			try {
				run(register(values));
			} catch (error: any) {
				message.error(error.message);
			}
		},
		1000
	);
	return (
		<>
			<Form onFinish={handSubmit}>
				<Form.Item
					name={'username'}
					rules={[{ required: true, message: '请输入用户名' }]}
					// eslint-disable-next-line react/no-children-prop
					children={<Input type='text' placeholder={'用户名'} id={'username'} />}
				/>
				<Form.Item
					name={'password'}
					rules={[{ required: true, message: '请输入密码' }]}
					// eslint-disable-next-line react/no-children-prop
					children={<Input placeholder={'密码'} type='password' id={'password'} />}
				></Form.Item>
				<Form.Item
					name={'cpassword'}
					rules={[{ required: true, message: '请确认密码' }]}
					// eslint-disable-next-line react/no-children-prop
					children={<Input placeholder={'请确认密码'} type='password' id={'cpassword'} />}
				></Form.Item>
				<LongButton {...{ loading: isLoading }} htmlType={'submit'} type={'primary'}>
					注册
				</LongButton>
			</Form>
		</>
	);
};
/**
 *登陆注册简单实现思路
 先简单定义2个组件，一个登陆前的组件，一个登陆后的组件
 登陆前的组件，我们分为登陆以及注册，可以设置一个状态isRegister默认为false，三元表达式。为true展示注册组件，同时下方有一个Button按钮，来帮我们切换状态取反展示不同的组件
 登陆后的组件。可以为我们的首页等等
 在我们的根index列表页，可以根据用户来三元表达，用JSX，如果用户存在我们使用登陆后的组件，反之，使用登录前的组件
 *
 */
