import React, { ReactElement, useState } from 'react';
import { useKanbanQueryKey, useProjectIdInUrl } from '../../../utils';
import { useAddKanban } from 'api/modules/jira/kanban';
import { Input } from 'antd';
import { Container } from './KanbanColumn';
export const CreateKanban = (): ReactElement => {
  const [name, setName] = useState('');
  const projectId = useProjectIdInUrl();
  const { mutateAsync: addkanban } = useAddKanban(useKanbanQueryKey());
  //   点击提交服务器，并且清空name''
  const submit = async () => {
    await addkanban({ name, projectId });
    setName('');
  };
  return (
    <Container>
      <Input
        size={'large'}
        placeholder="新建看板名称"
        // 键盘输入
        onChange={(eve) => setName(eve.target.value)}
        // 回车健提交服务器信息
        onPressEnter={submit}
        value={name}
      />
    </Container>
  );
};
