import React from 'react';
import { useTaskTypes, useTasks } from 'api/modules/jira/task';
import { Kanban } from 'type/jira/kanban';
import { useKanbanQueryKey, useTasksModal, useTasksSearchParams } from '../../../utils';
import styled from 'styled-components';
import { Card, Dropdown, Menu, Modal } from 'antd';
import { CreateTask } from './CreateTask';
import { flexCenter } from 'style/CommonCss';
import { Task } from 'type/jira/task';
import { Mark } from 'components/Mark';
import { useDeleteKanban } from 'api/modules/jira/kanban';
import { Button } from 'style/componends/Button';
import { CenterBox, ItemCenterBox } from 'style/componends/Flex';
import { Drag, Drop, DropChild } from 'components/DragAntDrop';

// 通过传入的id,来过滤掉数据
export const KanbanColumn = React.forwardRef<HTMLDivElement, { kanban: Kanban }>(
  ({ kanban, ...props }, ref) => {
    const { data: AllTasks } = useTasks(useTasksSearchParams());
    const tasks = AllTasks?.filter((task) => task.kanbanId === kanban.id);
    return (
      <Container {...props} ref={ref}>
        <Title>
          <CenterBox>{kanban.name}</CenterBox>
          <More {...{ kanban }} key={kanban.id} />
        </Title>
        <TasksContainer>
          <Drop type="ROW" direction="vertical" droppableId={String(kanban.id)}>
            <DropChild style={{ minHeight: '1rem' }}>
              {tasks?.map((task, taskIndex) => (
                <Drag key={task.id} index={taskIndex} draggableId={String('task' + task.id)}>
                  <div>
                    <TaskCard {...{ task }} key={task.id} />
                  </div>
                </Drag>
              ))}
            </DropChild>
          </Drop>
          <CreateTask {...{ kanbanId: kanban.id }} />
        </TasksContainer>
      </Container>
    );
  }
);

export const TaskCard = ({ task }: { task: Task }) => {
  const { startEdit } = useTasksModal();
  const { name: keyword } = useTasksSearchParams();
  return (
    <Card
      style={{ marginBottom: '0.5rem', cursor: 'pointer' }}
      key={task.id}
      onClick={() => startEdit(task.id)}
    >
      <p>
        <Mark {...{ keyword, name: task.name }} />
      </p>
      <TaskTypeIcon {...{ id: task.typeId }}></TaskTypeIcon>
    </Card>
  );
};
export const TaskTypeIcon = ({ id }: { id: number }) => {
  const { data: taskType } = useTaskTypes();
  const name = taskType?.find((taskType) => taskType.id === id)?.name;
  if (!name) {
    return null;
  }
  return (
    <img
      src={
        name === 'task'
          ? 'https://sindu12jun.github.io/static/media/bug.5d8e8fd5.svg'
          : 'https://sindu12jun.github.io/static/media/task.69774797.svg'
      }
      alt={'icon'}
    />
  );
};

const More = ({ kanban }: { kanban: Kanban }) => {
  const { mutateAsync } = useDeleteKanban(useKanbanQueryKey());
  const startDelete = () => {
    Modal.confirm({
      okText: '确定',
      cancelText: '取消',
      title: '确定删除看板吗',
      onOk() {
        return mutateAsync({ id: kanban.id as number });
      },
    });
  };
  const overlay = (
    <Menu>
      <Menu.Item>
        <Button onClick={startDelete}>删除</Button>
      </Menu.Item>
    </Menu>
  );
  return (
    <Dropdown overlay={overlay}>
      <Button bg="#f7f3f35e">
        <ItemCenterBox style={{ paddingBottom: '5px' }}>...</ItemCenterBox>
      </Button>
    </Dropdown>
  );
};
export const Container = styled.div`
  min-width: 27rem;
  border-radius: 6px;
  background-color: rgb(244, 245, 247);
  display: flex;
  flex-direction: column;
  padding: 0.7rem 0.7rem 1rem;
  margin-right: 1.5rem;
  height: 65vh;
`;
const TasksContainer = styled.div`
  overflow: scroll;
  flex: 1;

  ::-webkit-scrollbar {
    display: none;
  }
  .ant-card-bordered {
    margin: 0;
    border-radius: 1rem !important;
  }
`;
export const Title = styled.h1`
  background: ${() =>
    `rgba(${Math.ceil(Math.random() * 200)},${Math.ceil(Math.random() * 200)},${Math.ceil(
      Math.random() * 200
    )})`};
  border-radius: 1rem;
  padding: 1rem;
  margin-bottom: 10px;
  ${flexCenter};
  color: ${(props) => props.theme.lightText};
  transition: transform 0.2s linear, box-shadow 0.4s linear;
  &:hover {
    box-shadow: 0 0 0.5rem 0 #666;
    transform: translateY(-2px);
  }
  ${(props) => props.theme.mode === 'dark' && 'filter: brightness(0.5);'};
`;
