import React from 'react';
import { Button, Dropdown, Menu } from 'antd';
import { useNavigate } from 'react-router-dom';
import { ProjectModal } from '../scroon/ProjectModal';
import { ButtonNoPadding } from 'components/Row/Row';
import Scroon from '../scroon';
import { AuthenticatedIndexApp } from './index';
import { ReactComponent as SoftwareLogo } from 'assets/images/software-logo.svg';
import { useAuth } from 'utils/hooks/useContext/authContext';
import { useCampusTagKeyUrl, useDocumentTitle } from 'utils/hooks/authUtils';
import { cleanAllStorageButExclude } from 'utils/index/localStorage/setStorage';
import { CenterBox } from 'style/componends/Flex';
import { Container, Main, Header, HeaderLeft, HeaderRight } from './style';
import { ProjectPover } from 'components/ProjectPover/ProjectPover';
import { UserPover } from 'components/ProjectPover/UserPover';

const AuthenticatedApp = () => {
  useDocumentTitle('Project List', false);
  const { type } = useCampusTagKeyUrl();
  const USerInfo = () => {
    const { user } = useAuth();
    const logout = () => {
      cleanAllStorageButExclude();
      window.location.reload();
    };
    return (
      <Dropdown
        overlay={
          <Menu>
            <Menu.Item key={'logout'}>
              <Button style={{ display: 'block', margin: ' auto' }} onClick={logout}>
                登出
              </Button>
            </Menu.Item>
          </Menu>
        }
      >
        <Button type={'link'} style={{ color: 'black' }} onClick={(e) => e.preventDefault()}>
          Hi,欢迎{<span style={{ color: 'red' }}>{user?.name}</span>}登入
        </Button>
      </Dropdown>
    );
  };
  const Headers = () => {
    const navigate = useNavigate();
    const resetRouter = () => {
      navigate('/jira');
    };
    return (
      <>
        <Header between>
          <HeaderLeft gap>
            <ButtonNoPadding style={{ padding: 0 }} type={'link'} onClick={resetRouter}>
              <CenterBox>
                <SoftwareLogo width={'18rem'} color={'rgb(38,132,255)'} />
              </CenterBox>
            </ButtonNoPadding>
            <ProjectPover />
            <UserPover />
          </HeaderLeft>
          <HeaderRight>
            <USerInfo />
            <ProjectModal />
          </HeaderRight>
        </Header>
      </>
    );
  };
  const diffJiraSelectComp = type === 'kanban' || type === 'epic';
  return (
    <Container>
      <Headers />
      <Main>{diffJiraSelectComp ? <Scroon /> : <AuthenticatedIndexApp />}</Main>
    </Container>
  );
};

export default AuthenticatedApp;
