import { QueryKey, useMutation, useQuery, useQueryClient } from 'react-query';
import { Task, TaskType } from 'type/jira/task';
import { Kanban } from 'type/jira/kanban';
import { useHttp } from 'utils/hooks/useHttp';
import { useAddConfig, useDeleteConfig, useEditConfig } from 'utils/hooks/useOptimisticOptions';
//Task List
export const useTasks = (param?: Partial<Task>) => {
  const client = useHttp();
  return useQuery<Task[]>(['tasks', param], () => client('tasks', { data: param }));
};
// 选择Task类型 select组件使用
export const useTaskTypes = (param?: Partial<TaskType>) => {
  const client = useHttp();
  // 这里定义的ts,是返回值类型
  return useQuery<TaskType[]>(['taskTypes', param], () => client('taskTypes'));
};

// 添加大Task
export const useAddTask = (queryKey: QueryKey) => {
  const client = useHttp();
  // const queryClient = useQueryClient();
  return useMutation(
    (param: Partial<Task>) =>
      client(`tasks`, {
        data: param,
        method: 'POST',
      }),
    // 这2个效果是一样的，都是乐观更新
    // {
    //   onSuccess: () => queryClient.invalidateQueries('tasks'),
    // }
    useAddConfig(queryKey)
  );
};
// Task编辑
export const useTask = (id?: number) => {
  const client = useHttp();
  return useQuery<Task[]>(['task', { id }], () => client(`tasks/${id}`), {
    enabled: !!id,
  });
};

export const useEditTask = (queryKey: QueryKey) => {
  const client = useHttp();
  return useMutation(
    (param: Partial<Task>) =>
      client(`tasks/${param.id}`, {
        method: 'PATCH',
        data: param,
      }),
    useEditConfig(queryKey)
  );
};

//删除Tasks
export const useDeleteTask = (queryKey: QueryKey) => {
  const client = useHttp();
  return useMutation(
    ({ id }: { id: number }) =>
      client(`tasks/${id}`, {
        method: 'DELETE',
      }),
    useDeleteConfig(queryKey)
  );
};
