import React, { useState, useEffect, useMemo } from 'react';
import { List } from './list';
import { SearchPanel } from './saerchPanel';
import { useDebounce } from 'utils/hooks/authUtils';
import { ContainerSearch } from './style';
import { Typography } from 'antd';
import { useProjectoModal } from 'utils/hooks/useUrlQueryParam';
import { useProjects, useProjectsSearchParams, useUsers } from 'api/modules/jira/homeList';
import { ButtonNoPadding, ErrorBox, Row } from 'components/Row/Row';
export const AuthenticatedIndexApp = () => {
  //存储用户信息
  const { open } = useProjectoModal();
  const [param, setParam] = useProjectsSearchParams();
  //这里用防抖来进行限制请求
  const debouncedValue = useDebounce(param, 1000);
  const { data: user } = useUsers();
  const { isLoading, error, data: list } = useProjects(debouncedValue);
  return (
    <ContainerSearch>
      <Row between>
        <h1 style={{ fontSize: '24px' }}>项目列表</h1>
        <ButtonNoPadding onClick={open} type={'link'}>
          创建项目
        </ButtonNoPadding>
      </Row>
      <SearchPanel {...{ param, setParam, user: user || [] }} />
      <ErrorBox {...{ error }} />
      <List
        {...{
          user: user || [],
          loading: isLoading,
          dataSource: list || [],
        }}
      />
    </ContainerSearch>
  );
};
