import React, { useCallback } from 'react';
import { useDocumentTitle } from 'utils/hooks/authUtils';
import {
  useKanbanQueryKey,
  useKanbanSearchParams,
  useProjectInUrl,
  useRecorderKanban,
  useRecorderTask,
  useTasksQueryKey,
  useTasksSearchParams,
} from '../../utils';
import { useKanbans } from 'api/modules/jira/kanban';
import { KanbanColumn } from './components/KanbanColumn';
import styled from 'styled-components';
import { SearchPanel } from './components/SearchPence';
import { useTasks } from 'api/modules/jira/task';
import { Spin } from 'antd';
import { CenterBox } from 'style/componends/Flex';
import { CreateKanban } from './components/CreateKanban';
import { TaskModal } from './components/TaskModal';
import { Caption } from 'style/componends/Text';
import { DragDropContext, DropResult } from 'react-beautiful-dnd';
import { Drag, Drop, DropChild } from 'components/DragAntDrop';
export const KanBanScroon = () => {
  // 浏览器标题更换
  useDocumentTitle('看板列表');
  const { data: currendProject } = useProjectInUrl();
  const { data: kanbans, isLoading: kanbanIsLoading } = useKanbans(useKanbanSearchParams());
  const { isLoading: taskIsLoading } = useTasks(useTasksSearchParams());
  const onDragEnd = useDragEnd();
  const isLoading = taskIsLoading || kanbanIsLoading;
  if (isLoading) {
    // eslint-disable-next-line react/no-children-prop
    return <CenterBox children={<Spin size={'large'} />} />;
  }
  return (
    <DragDropContext onDragEnd={onDragEnd}>
      <Container>
        <Caption style={{ paddingBottom: '15px' }}>{currendProject?.name}看板</Caption>
        <SearchPanel />
        <ColumnContainer>
          <Drop type="COLUMN" direction="horizontal" droppableId={'kanban'}>
            <DropChild style={{ display: 'flex' }}>
              {kanbans?.map((kanban, index) => (
                <Drag
                  key={`${kanban.id}${index}`}
                  draggableId={String(`${kanban.id}${index}`)}
                  index={index}
                >
                  <KanbanColumn key={kanban.id} {...{ kanban }} />
                </Drag>
              ))}
            </DropChild>
          </Drop>
          <CreateKanban />
        </ColumnContainer>
        <TaskModal />
      </Container>
    </DragDropContext>
  );
};

export const useDragEnd = () => {
  const { data: kanbans } = useKanbans(useKanbanSearchParams());
  const { mutate: recordKanban } = useRecorderKanban(useKanbanQueryKey());
  const { mutate: recordTask } = useRecorderTask(useTasksQueryKey());

  const { data: allTasks = [] } = useTasks(useTasksSearchParams());
  return useCallback(
    ({ source, destination, type }: DropResult) => {
      if (!destination) return;
      if (type === 'COLUMN') {
        const fromId = kanbans?.[source.index].id;
        const toId = kanbans?.[destination.index].id;
        if (!fromId || !toId || fromId === toId) return;
        const type = destination.index > source.index ? 'after' : 'before';
        recordKanban({ fromId, referenceId: toId, type });
      }
      if (type === 'ROW') {
        const fromKanbanId = +source.droppableId;
        const toKanbanId = +destination.droppableId;
        if (fromKanbanId === toKanbanId) return;
        const fromTask = allTasks?.filter((task) => task.kanbanId === fromKanbanId)[source.index];
        const toTask = allTasks?.filter((task) => task.kanbanId === toKanbanId)[destination.index];
        if (fromTask?.id === toTask.id) {
          return;
        }
        recordTask({
          fromId: fromTask?.id,
          referenceId: toTask?.id,
          fromKanbanId,
          toKanbanId,
          type:
            fromKanbanId === toKanbanId && destination.index > source.index ? 'after' : 'before',
        });
      }
    },

    [allTasks, kanbans, recordKanban, recordTask]
  );
};
export const ColumnContainer = styled.div`
  display: flex;
  overflow-x: scroll;
  margin-right: 2rem;
  overflow-x: scroll;
`;
export const Container = styled.div`
  padding: 10px;
  width: 100%;
  display: flex;
  flex-direction: column;
`;
