import { Form, Input, Select } from 'antd';
import { Lists } from './list';
import { UserSelect } from 'components/UserSecelt/UserSecelt';
type User = {
  name: string;
  email?: string;
  id: number;
  title?: string;
  organization?: string;
  token?: string;
};
type SearchPanelProps = {
  // param:Partial<Pick<Lists,'name'|"personId">>
  param: any;
  user: User[];
  setParam: (param: SearchPanelProps['param']) => void;
};
//假如只有一个参数，可以直接后面定义ts类型，在类型里面定义需要结构的值(param:xxx,user:xxx)==>(param:SearchProps),在fun里面在重新结构出来
//假如是已经直接在形参下结构了，可以直接定义一个ts类型，ts里面定义已经结构了的值{param,user}:{param:xxx,user:xxx}==>{param,user}:SearchProps
export const SearchPanel = ({ param, user, setParam }: SearchPanelProps) => {
  return (
    <>
      <Form style={{ marginBottom: '1.5rem' }} layout={'inline'}>
        <Form.Item>
          <Input
            type="text"
            placeholder={'项目名'}
            value={param.name}
            onChange={(eve) =>
              setParam({
                ...param,
                name: eve.target.value,
              })
            }
          />
        </Form.Item>
        <Form.Item>
          <UserSelect
            defaultOptionName={'负责人'}
            value={param.personId}
            onChange={(value) =>
              setParam({
                ...param,
                personId: value,
              })
            }
          />
        </Form.Item>
      </Form>
    </>
  );
};
/**
 * React中的hooks，不管是自带的，还是第三方库，前面必须加use开头，这是规定，
 * 而且hooks不可以在普通函数里面运行，也就是不能放在里面
 *
 */
