import React from 'react';
import { Container } from '../kanban/KanBan';
import { Caption } from 'style/componends/Text';
import { useEpicQueryKey, useProjectInUrl } from '../../utils';
import { useDeleteEpic, useEpics } from 'api/modules/jira/epic';
import { List, Modal } from 'antd';
import { Row } from 'components/Row/Row';
import { Button } from 'style/componends/Button';
import moment from 'moment';
import { useTasks } from 'api/modules/jira/task';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import { BetweenBox, ItemCenterBox } from 'style/componends/Flex';
import { Epic } from 'type/jira/epic';
import { CreateEpic } from './CreateEpic';
import { useSafeSetState } from 'utils/hooks/authUtils';
export const EcipScroon = () => {
  const { data: currentProject } = useProjectInUrl();
  const { data: epics } = useEpics();
  const { mutate: deleteEpic } = useDeleteEpic(useEpicQueryKey());
  const { data: tasks } = useTasks({ projectId: currentProject?.id });
  const [epicCreateOpen, setEpicCreateOpen] = useSafeSetState(false);
  const confirmDeleteEpic = (epic: Epic) => {
    Modal.confirm({
      title: `确定删除项目组：${epic.name}`,
      content: '点击确定删除',
      okText: '确定',
      onOk() {
        deleteEpic({ id: epic.id });
      },
    });
  };
  return (
    <Container>
      <BetweenBox>
        <Caption>{currentProject?.name}任务组</Caption>
        <Button onClick={() => setEpicCreateOpen(true)}>创建任务组</Button>
      </BetweenBox>
      <List
        dataSource={epics}
        itemLayout="vertical"
        renderItem={(epic) => (
          <List.Item>
            <List.Item.Meta
              title={
                <Row between>
                  <span>{epic.name}</span>
                  <Button onClick={() => confirmDeleteEpic(epic)}>删除</Button>
                </Row>
              }
              description={
                <>
                  <ItemCenterBox>开始时间:{moment(epic.start).format('YYYY-MM-DD')}</ItemCenterBox>
                  <ItemCenterBox>结束时间:{moment(epic.end).format('YYYY-MM-DD')}</ItemCenterBox>
                </>
              }
            />
            <ListLink>
              {tasks
                ?.filter((task) => task.epicId === epic.id)
                .map((task) => (
                  <Link to={''} key={task.id}>
                    <ItemCenterBox> {task?.name}</ItemCenterBox>
                  </Link>
                ))}
            </ListLink>
          </List.Item>
        )}
      ></List>
      <CreateEpic onClose={() => setEpicCreateOpen(false)} visible={epicCreateOpen} />
    </Container>
  );
};
export const ListLink = styled.div`
  a {
    color: #1890ff;
  }
`;
