import React, { useEffect, useMemo } from 'react';
import { Divider, List, Popover, Typography } from 'antd';
import { ButtonNoPadding } from '../Row/Row';

import { useProjectoModal } from 'utils/hooks/useUrlQueryParam';
import { useProjects } from 'api/modules/jira/homeList';
import styled from '@emotion/styled';
export const ProjectPover = () => {
  const { open } = useProjectoModal();
  const { data } = useProjects();

  const pinnedProjects = useMemo(() => {
    return data?.filter((item: { pin: boolean }) => item.pin);
  }, [data]);
  const Content = (
    <Container>
      <Typography.Text type={'secondary'}>收藏项目</Typography.Text>
      <List>
        {pinnedProjects?.map((item: any) => (
          <List.Item key={item.id}>
            <List.Item.Meta title={item.name} />
          </List.Item>
        ))}
      </List>

      <Divider />
      <ButtonNoPadding onClick={open} type={'link'}>
        创建项目
      </ButtonNoPadding>
    </Container>
  );
  return (
    <Popover placement={'bottom'} content={Content}>
      <span>项目</span>
    </Popover>
  );
};
const Container = styled.div`
  min-width: 20rem;
`;
