import styled from '@emotion/styled';
import { Button, Drawer, Form, Input, Spin } from 'antd';
import { useForm } from 'antd/es/form/Form';
import { useAddProject, useEditProject, useProjectQueryKey } from 'api/modules/jira/homeList';
import { ErrorBox } from 'components/Row/Row';
import { UserSelect } from 'components/UserSecelt/UserSecelt';
import React, { useEffect } from 'react';
import { useProjectoModal } from 'utils/hooks/useUrlQueryParam';
export const ProjectModal = () => {
  const { ProjectModalOpen, clone, editProject, isLoading } = useProjectoModal();
  const title = editProject ? '编辑项目' : '创建项目';
  const useMutateProject = editProject ? useEditProject : useAddProject;
  const { mutateAsync, error, isLoading: mutateLoading } = useMutateProject(useProjectQueryKey());
  const [form] = useForm();
  const onFinish = (values: { name: string; organization: string; personId: number }) => {
    mutateAsync({ ...editProject, ...values }).then(() => {
      form.resetFields();
      clone();
    });
  };
  const cloneModal = () => {
    form.resetFields();
    clone();
  };
  useEffect(() => {
    form.setFieldsValue(editProject);
  }, [form, editProject]);

  return (
    <Drawer
      forceRender
      visible={ProjectModalOpen}
      placement="right"
      onClose={cloneModal}
      width={'100%'}
    >
      {isLoading ? (
        <Spin size={'large'} />
      ) : (
        <>
          <h1 style={{ margin: 'auto', width: '12rem' }}>{title}</h1>
          <ErrorBox {...{ error }} />
          <Form
            form={form}
            layout={'vertical'}
            style={{ width: '40rem', margin: 'auto' }}
            onFinish={onFinish}
          >
            <Form.Item
              label={'名称'}
              name={'name'}
              rules={[{ required: true, message: '请输入项目名称' }]}
            >
              <Input placeholder={'请输入项目名称'} />
            </Form.Item>
            <Form.Item
              label={'部门'}
              name={'organization'}
              rules={[{ required: true, message: '请输入部门名称' }]}
            >
              <Input placeholder={'请输入部门名称'} />
            </Form.Item>
            <Form.Item label={'负责人'} name={'personId'}>
              <UserSelect defaultOptionName={'负责人'} />
            </Form.Item>
            <Form.Item style={{ textAlign: 'right' }}>
              <Button loading={mutateLoading} type={'primary'} htmlType={'submit'}>
                提交
              </Button>
            </Form.Item>
          </Form>
        </>
      )}
    </Drawer>
  );
};
